@import url("https://fonts.googleapis.com/css2?family=Merriweather:wght@700&family=Montserrat:wght@400;500;700&display=swap");
.container {
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
}

@media (min-width: 1240px) {
  .container {
    max-width: 1240px;
  }
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 30px 0;
  color: #C8CDD8;
  font-weight: 500;
  overflow: hidden;
  margin-bottom: 115px;
}

.navbar a {
  color: #C8CDD8;
}

.navbar__logo {
  display: flex;
  align-items: flex-end;
  padding-left: 10px;
}

.navbar__logo img, .navbar__logo span {
  position: relative;
  z-index: 10;
}

.navbar__logo-img {
  position: relative;
}

.navbar__logo-img::before {
  content: '';
  display: block;
  position: absolute;
  top: -30px;
  bottom: -30px;
  left: -10px;
  right: -10px;
  background: #6C525B;
  z-index: 5;
}

.navbar__name {
  transform: translateX(-18px);
  font-weight: bold;
  font-size: 20px;
  color: #FFFFFF;
}

.navbar__nav a {
  color: #FFFFFF;
  position: relative;
}

.navbar__nav a:not(:last-of-type) {
  margin-right: 30px;
}

.navbar__nav a:hover::before {
  content: '';
  display: block;
  position: absolute;
  height: 2px;
  bottom: -10px;
  left: 0;
  right: 0;
  background: #6C525B;
}

.navbar__social {
  font-size: 22px;
}

.navbar__social a:hover {
  color: #FFFFFF;
}

.main__contact:not(:last-of-type) {
  margin-right: 60px;
}

.main__footer {
  border-top: 1px solid #4D515E;
  padding: 30px 0 115px;
  color: #C8CDD8;
  display: flex;
}

.main__header {
  height: 350px;
  position: relative;
  max-width: 785px;
  color: #FFFFFF;
  display: flex;
  align-items: center;
  margin-bottom: 85px;
}

.main__header h1 {
  z-index: 10;
}

.main__header::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 385px;
  border: 7px solid #6C525B;
  box-sizing: border-box;
}

.main__phone {
  font-weight: bold;
  font-size: 16px;
}

.about {
  padding: 70px 0 100px;
  color: #2B2D33;
}

.about__bottom {
  display: flex;
}

.about__bottom-item {
  padding-top: 37px;
  border-top: 1px solid #BDC4CF;
}

.about__bottom-item h3 {
  margin-bottom: 27px;
}

.about__bottom-item:not(:last-of-type) {
  margin-right: 65px;
}

.about__top {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.about__top h2 {
  color: #2B2D33;
  margin-bottom: 36px;
}

.about__top-item:last-of-type {
  width: 48%;
  display: flex;
  justify-content: flex-end;
}

.about__top-item:last-of-type img {
  width: 453px;
  height: 326px;
}

.about__top-item:first-of-type {
  width: 52%;
}

.slider {
  padding: 70px 0;
}

.slider.slider_overflow {
  overflow: hidden;
}

.slider.slider_dark .slider__next,
.slider.slider_dark .slider__prev {
  border: 1px solid #2B2D33;
  color: #2B2D33;
}

.slider.slider_dark .slider__next.disable,
.slider.slider_dark .slider__prev.disable {
  color: #AEB2BA;
  border: 1px solid #AEB2BA;
}

.slider.slider_dark .slider__header {
  color: #2B2D33;
}

.slider.slider_news {
  padding: 70px 0 110px;
}

.slider.slider_news .slider__item {
  min-width: calc(100% / 3);
}

.slider.slider_news .slider__img {
  margin-bottom: 20px;
  height: 265px;
}

.slider.slider_news .slider__date {
  margin-bottom: 15px;
  font-size: 14px;
  color: #6C525B;
}

.slider.slider_news .slider__title {
  font-weight: bold;
  font-size: 18px;
}

.slider__content {
  display: flex;
}

.slider__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #FFFFFF;
  margin-bottom: 45px;
}

.slider__item {
  margin-right: 30px;
  min-width: calc((100% / 3) - 20px - 20px);
}

.slider__item:last-of-type {
  margin-right: 0;
}

.slider__img {
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
  height: 410px;
}

.slider__next, .slider__prev {
  background: transparent;
  border: 1px solid #FFFFFF;
  padding: 10px 8px;
  color: #FFFFFF;
}

.slider__next:not(:last-of-type), .slider__prev:not(:last-of-type) {
  margin-right: 10px;
}

.slider__next.disable, .slider__prev.disable {
  cursor: default;
  color: #585E67;
  border: 1px solid #585E67;
}

.menu {
  padding: 10px 0 120px;
}

.menu__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 40px;
}

.menu__header img {
  transform: translateY(70px);
  width: 188px;
  height: 166px;
}

.tabs__body {
  padding-top: 50px;
}

.tabs__body-item {
  display: none;
  flex-wrap: wrap;
  align-items: flex-start;
  margin: 0 -5px;
}

.tabs__body-item.active {
  display: flex;
}

.tabs__head {
  border-bottom: 1px solid #BDC4CF;
  display: flex;
}

.tabs__head-item {
  display: block;
  padding: 15px 20px;
  font-weight: bold;
  font-size: 18px;
  transform: translateY(1px);
}

.tabs__head-item.active {
  background: #6C525B;
  color: #FFFFFF;
}

.card {
  padding: 40px 30px;
  border: 4px solid #BDC4CF;
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% / 3 - 10px);
}

.card:not(:nth-last-child(-n+3)) {
  margin-bottom: 10px;
}

.card__head {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  font-size: 17px;
  margin-bottom: 60px;
}

.card__line {
  margin-bottom: 10px;
}

.card__line span {
  font-weight: 500;
}

.card__price {
  color: #6C525B;
}

.adv {
  padding: 90px 0;
  background: url("../img/adv.png") 0 center no-repeat;
}

.adv__body li {
  margin-bottom: 35px;
  position: relative;
  padding-left: 40px;
}

.adv__body li::before {
  content: '';
  position: absolute;
  left: 0;
  top: calc(50% - 13px);
  width: 19px;
  height: 27px;
  background: url("../img/list-style.svg") center center no-repeat;
}

.adv__container {
  display: flex;
  justify-content: flex-end;
}

.adv__card {
  width: calc(50% + 60px);
  background: #2B2D33;
  color: #FFFFFF;
  padding: 60px 60px 80px;
}

.adv__head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}

.news {
  color: #2B2D33;
}

.banner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 65px 0;
}

.banner__btn .las {
  font-size: 1.2rem;
  transform: translate(5px, 1.5px);
}

.banner__content {
  max-width: 685px;
}

.banner__content h2 {
  margin-bottom: 20px;
  color: #FFFFFF;
  font-size: 24px;
}

.banner__content p {
  color: #C8CDD8;
  font-size: 18px;
}

.contacts {
  display: flex;
}

.contacts__btns {
  display: flex;
}

.contacts__btns .btn:not(:last-of-type) {
  margin-right: 15px;
}

.contacts__btns .btn .lab {
  transform: translate(-5px, 0px);
  font-size: 1.8rem;
  color: #585E67;
}

.contacts__content {
  width: 45%;
  padding: 100px 0 130px 70px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  max-width: 500px;
}

.contacts__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.contacts__header h2 {
  padding-top: 20px;
}

.contacts__items .contact {
  margin-bottom: 20px;
}

.contacts__items .contact:last-of-type {
  margin-bottom: 50px;
}

.contacts__map {
  width: 55%;
  background: url("../img/map.png") center center no-repeat;
  background-size: cover;
}

* {
  padding: 0;
  margin: 0;
  transition: all 0.2s;
  box-sizing: border-box;
}

html {
  font-size: 15px;
  scroll-behavior: smooth;
  font-family: 'Montserrat', sans-serif;
}

button {
  cursor: pointer;
  font-family: 'Montserrat', sans-serif;
}

img {
  display: block;
}

p {
  line-height: 26px;
}

h1, h2 {
  font-family: 'Merriweather', serif;
  font-weight: bold;
}

h1 {
  font-size: 58px;
}

h2 {
  font-size: 36px;
}

a {
  text-decoration: none;
  color: inherit;
}

ul li {
  list-style: none;
}

.section {
  background-size: cover;
  background-position: center top;
  background-repeat: no-repeat;
}

.section_main {
  background-image: url("../img/bg-main.png");
}

.section_about,
.section_menu,
.section_adv,
.section_news {
  background-color: #E3E5EA;
}

.section_slider {
  background-image: url("../img/bg-slider.png");
}

.section_banner {
  background-image: url("../img/bg-banner.png");
}

.btn {
  padding: 20px 30px;
  border-radius: none;
  border: none;
  background: transparent;
  color: #2B2D33;
  display: flex;
  align-items: center;
}

.btn.btn_red {
  background: #6C525B;
  color: #FFFFFF;
}

.btn.btn_red:hover {
  background: #846A73;
}

.btn.btn_dark {
  background: #2B2D33;
  color: #FFFFFF;
}

.btn.btn_dark:hover {
  background: #444853;
}

.contact {
  display: flex;
}

.contact__icon {
  width: 26px;
}

.contact__content span {
  display: block;
}
